<template>
    <div class="myTopup">
        <van-nav-bar
            title="充值咖啡钱包"
            right-text="更多优惠"
            left-arrow
            :border='false'
            @click-left="goBack"
            @click-right="onClickRight"
            color="#000"
            />

            <ul class="Bodys">
                <li v-for="(item,index) in bankDats" :key="index">
                    <div class="heads"><span>￥</span>{{item.proic}}</div>
                    <div class="texts">
                        全国通用券
                        <van-icon name="info-o" />
                    </div>
                    <van-stepper @change="ff_sum" v-model="item.value" theme="round" button-size="22" disable-input />
                </li>
               
            </ul>

            

            <van-submit-bar :price="total" button-text="去结算">
                <template #tip>
                    <van-icon name="info-o" />
                    购买以上商品券享<span> 充5赠3 </span>,不同商品券可组合购买。
                </template>
            </van-submit-bar>
    </div>
</template>

<script>
    export default {
        name:'Topup',
        data() {
            return {
                bankDats:[
                    {proic:33,value:1},
                    {proic:43,value:1},
                    {proic:23,value:1},
                    {proic:66,value:1}, 
                ],
                 total : 0
            }
        },
        created() {
            this.ff_sum();
        },
        methods: {
            goBack(){
                this.$router.go(-1);
            },
            onClickRight(){
                this.$router.push({name:'Alliance'});
            },
            ff_sum(){
               let sum = 0;
               this.bankDats.forEach(Item=>{
                   sum += (Item.proic-0) * (Item.value-0)
               });
               this.total = sum *100;
            }

        },
        
    }
</script>

<style lang="less" scoped>
     .myTopup{
         background-color: #f5f5f5;
         width: 100%;
         height: 100%;

         /deep/.van-nav-bar{
             background-color: transparent;
             

             
         }
         /deep/.van-nav-bar__title{
             font-size: 19px; 
         }
         /deep/.van-nav-bar__text{
             color: #000;
             font-size: 13px;
             font-weight: 540;
         }

         /deep/.van-icon-arrow-left{
             font-size: 20px;
             color: #000;
         }
         .Bodys{
             padding: 20px 20px;

             li{
                 margin: 6px 0px;
                 background-color: #fff;
                 display: flex;
                 height: 80px;
                 border-radius: 8px;
                 overflow: hidden;
                 position: relative;

             }
             .heads{
                 height: 100%;
                 width: 30%;
                 background: url(../assets/img/snipaste20201110_195344.png) no-repeat 0px 0px;
                 background-size: 100%;
                 color: #7b552b;
                 font-size: 32px;
                 font-weight: 550;
                 text-align: center;
                 line-height: 80px;
                 position: relative;

                 span{
                            font-size: 17px;
                            position: absolute;
                            top: -6px;
                            left: 14px;
                            font-weight: 400;
                 }
             }
            .texts{
                font-size: 16px;
                font-weight: 550;
                box-sizing: border-box;
                padding: 31px 13px;
                

            }
            /deep/.van-icon{
                top: 2px;
                font-size: 14px;
            }
            /deep/.van-stepper--round{
                height: 22px;
                position: absolute;
                top: 40%;
                right: 15px;

            }
            /deep/.van-stepper__minus{
                border: 1px solid #6c87c4;
                color: #6c87c4;
            }
            /deep/.van-stepper__plus{
                background-color: #6c87c4;
            }

         }
       /deep/.van-submit-bar__text{
           >span{
               float: left;
           }
           >span:nth-child(1){
               font-size: 16px;
               top: 5px;
           }
           >span:nth-child(2){
               font-size: 17px;
           }
        
       }
         
         /deep/.van-submit-bar__button--danger{
            width: 30%;
            background: #c5cfe8;
            font-weight: 500;
            color: #fff;
        }
        /deep/.van-submit-bar__tip{
            color: #fff;
            background-color: #ef5821;
            text-align: left;
            padding: 5px 6px;
            font-size: 12px;

            >span{
                font-size: 15px;
            }

            .van-icon-info-o{
                position: relative;
                top: 1px;
               

            }


        }
     }
    //  #ef5821

</style>